import { useBirthdayStore } from "../../store/birthdayStore";
import { motion } from "framer-motion";

export const Birthday = () => {
	const { birthday, check } = useBirthdayStore();

	if (birthday) return null;

	const handleClick = () => {
		check();
	};

	return (
		<article className="birthday">
			<motion.div
				initial={{ x: 100, opacity: 0 }}
				animate={{
					x: 0,
					opacity: 1,
				}}
			>
				<h1>.   </h1>
				<p className="talk">
					,     . - ?
				</p>
			</motion.div>

			<motion.p
				className="p"
				initial={{
					x: 200,
					opacity: 0,
				}}
				animate={{ x: 0, opacity: 1 }}
				transition={{ delay: 0.1 }}
			>
				  .   -     ,
				    !
			</motion.p>

			<motion.p
				className="p"
				initial={{
					x: 200,
					opacity: 0,
				}}
				animate={{ x: 0, opacity: 1 }}
				transition={{ delay: 0.2 }}
			>
				 .  .
			</motion.p>

			<motion.p
				className="p"
				initial={{
					x: 200,
					opacity: 0,
				}}
				animate={{ x: 0, opacity: 1 }}
				transition={{ delay: 0.3 }}
			>
				      .      
				,     -  <b></b>  !
			</motion.p>

			<motion.p
				className="p"
				initial={{
					x: 200,
					opacity: 0,
				}}
				animate={{ x: 0, opacity: 1 }}
				transition={{ delay: 0.4 }}
			>
				   <b></b>.  ? (  -   
				)    ,   
				  .
			</motion.p>

			<motion.p
				className="p"
				initial={{
					x: 200,
					opacity: 0,
				}}
				animate={{ x: 0, opacity: 1 }}
				transition={{ delay: 0.5 }}
			>
				      .   ,{" "}
				<b>  </b>  <br /> (   
				)
			</motion.p>

			<motion.button
				type="button"
				className="button"
				onClick={handleClick}
				initial={{ y: 100, opacity: 0 }}
				animate={{ y: 0, opacity: 1 }}
				transition={{ delay: 2.5 }}
			>
				  ! 
			</motion.button>
		</article>
	);
};
